* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "montseratt", "sans-serif";
}

.app {
  margin: auto;
  text-align: center;
  background-image: url(./assets/winter2.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transition: 0.4 ease-out;
  /* TRANSITION */
  -webkit-transition: background 0.4s;
  -moz-transition: background 0.4s;
  -o-transition: background 0.4s;
  transition: background 0.4s;
}

.app.warm {
  background-image: url(./assets/warmer.jpg);
}

.app.cloud {
  background-image: url(./assets/cloud.jpg);
}

.app.rainy {
  background-image: url(./assets/rainy1.jpg);
}

.app.clear {
  background-image: url(./assets/clear-sky.jpg);
}

main {
  min-height: 100vh;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.75)
  );
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  outline: none;
  border: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  color: #313131;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(120, 120, 150, 0.5);
}

.location-box .date {
  color: #fff;
  font-size: 20px;
  font-weight: 200;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(120, 120, 150, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #fff;
  font-size: 102px;
  font-weight: 900px;

  text-shadow: 3px 6px rgba(120, 120, 150, 0.5);
  text-align: center;

  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(120, 120, 150, 0.5);
}
